* {
    font-family:Helvetica;
}
body {
    padding: 60px;
}
input {
    color: gray;
    font-size: 1.2rem;
    width:100%;
}
.card-grid {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
}
.card {
    align-content: center;
    border: 1px solid gray;
    border-radius: 6px;
    overflow: hidden;
    margin-bottom: 10px;
    margin-right: 10xp;
}
.card p {
    padding:5px;
    text-align: center;
}
.card img {
    max-height:170px;
}